
:host(limel-file-dropzone){
    position: relative;
    display: grid;;
    position: relative;
    height: 100%;
    width: 100%;
}

.has-file-to-drop{
   position: absolute;
   top: 0;
   left: 0;
   overflow: hidden;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   width: 100%;
   height: 100%;
   box-sizing: border-box;
 
   border: 0.25rem dashed rgb(var(--color-gray-light));
   border-radius: 1rem;
   background-color: rgb(var(--color-gray-dark));
            
    
   opacity: 0.9;
   margin: 0.125rem;
   isolation: isolate;
   z-index: 1;
            
   limel-icon.dropzone-icon {
        height: clamp(2rem, 4vh, 6rem);
        width: clamp(2rem, 4vh, 6rem);
        color: rgb(var(--contrast-800));
   }
            
   .dropzone-text{
        font-size: clamp(1rem, 2vh, 1.75rem);
        padding-top: 1rem;
        color: rgb(var(--contrast-800));
    }
}
